/* src/styles/Header.css */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #cde9ff; /* Navy blue from the logo */
  color: white;
}

.logo img {
  height: 80px; /* Adjust the height as needed */
  width: auto; /* Maintain the aspect ratio */
}

.nav ul {
  list-style: none;
  display: flex;
  gap: 20px;
  padding: 0;
  margin: 0;
}

.nav a {
  color: white;
  text-decoration: none;
  font-size: 1em;
  font-weight: bold; /* Make the text thicker */
  padding: 10px 20px; /* Add padding for button effect */
  border: 2px solid #37e773; /* Add green border from the logo */
  border-radius: 5px; /* Round the corners */
  background-color: #1e3a8a; /* Match the background color */
  transition: background-color 0.3s, color 0.3s; /* Smooth transition */
}

.nav a:hover {
  background-color: #10b981; /* Change background color on hover */
  color: #1e3a8a; /* Change text color on hover */
  text-decoration: none;
}
.nav a:hover {
  text-decoration: underline;
}

.logo img {
  height: 150px; /* Adjust the height as needed */
  width: 150px; /* Adjust the width as needed */
}
